
import { computed, defineComponent, onMounted, nextTick, ref } from "vue";
import { hideModal } from "@/core/helpers/dom";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { Actions } from "@/store/enums/StoreEnums";
import { StepperComponent } from "@/assets/ts/components/_StepperComponent";
import Logo from "@/components/Logo.vue";
import Dropdown3 from "@/components/dropdown/Dropdown3.vue";
import Swal from "sweetalert2/dist/sweetalert2.min.js";
import { useForm, Field, ErrorMessage } from "vee-validate";
import { useI18n } from "vue-i18n/index";
import * as Yup from "yup";
import { reinitializeComponents } from "@/core/plugins/keenthemes";
import ApiService from "@/core/services/ApiService";
import { AxiosRequestConfig } from "axios";

// Account type
interface Step1 {
  account_type: string;
}

// Account info
interface Step2 {
  invoice_plan: string;
  name: string;
  email: string;
  phone: string;
  password: string;
}

// Enterprise Info
interface Step3 {
  market_name: string;
  market_address: string;
  market_tax_number: string;
  market_commercial_number: string;
  market_standard_number: string;
  market_site_url: string;
  market_email: string;
  market_phone: string;
}

interface KTCreateApp extends Step1, Step2, Step3 {}

export default defineComponent({
  name: "sign-up",
  components: {
    Field,
    ErrorMessage,
    Logo,
    Dropdown3,
  },
  setup() {
    const _stepperObj = ref<StepperComponent | null>(null);
    const createAccountRef = ref<HTMLElement | null>(null);
    const createAccountModalRef = ref<HTMLElement | null>(null);
    const currentStepIndex = ref(0);
    const imgPreview = ref<string>("");
    const store = useStore();
    const router = useRouter();

    const { t, te } = useI18n();
    // market_image file
    const market_image = ref<File | null>(null);
    const formData = ref<KTCreateApp>({
      account_type: "cloud",
      invoice_plan: "100",
      name: "",
      email: "",
      phone: "",
      password: "",
      market_name: "",
      market_address: "",
      market_tax_number: "",
      market_commercial_number: "",
      market_standard_number: "",
      market_site_url: "",
      market_email: "",
      market_phone: "",
    });

    onMounted(() => {
      nextTick(() => {
        reinitializeComponents();
      });

      _stepperObj.value = StepperComponent.createInsance(
        createAccountRef.value as HTMLElement
      );
    });

    const translate = (text) => {
      if (te(text)) {
        return t(text);
      } else {
        return text;
      }
    };

    const countries = {
      en: {
        flag: "/media/flags/united-states.svg",
        name: "English",
      },
      ar: {
        flag: "/media/flags/saudi-arabia.svg",
        name: "Arabic",
      },
    };

    const currentLanguage = computed(() => {
      return store.getters.getLanguage;
    });

    const currentLangugeLocale = computed(() => {
      return countries[currentLanguage.value];
    });

    const createAppSchema = [
      Yup.object({
        account_type: Yup.string().required("Account Type is required"),
      }),

      Yup.object({
        invoice_plan: Yup.string().required("Invoice Plan is required"),
        name: Yup.string().required("Name is required"),
        password: Yup.string()
          .min(8, "Password must be at least 8 characters")
          .required("Password is required"),
        phone: Yup.string()
          .matches(/^[0-9]/, "Phone number must be 10 digits long")
          .min(10, "Phone number must be 10 digits long")
          .required("Phone number is required"),
        email: Yup.string()
          .email("Invalid email")
          .required("Email is required"),
      }),

      Yup.object({
        market_name: Yup.string().required().label("Enterprise Name"),
        market_address: Yup.string().required().label("Enterprise Address"),
      }),
    ];

    // extracts the individual step schema
    const currentSchema = computed(() => {
      return createAppSchema[currentStepIndex.value];
    });

    const totalSteps = computed(() => {
      if (!_stepperObj.value) {
        return;
      }

      return _stepperObj.value.totatStepsNumber;
    });

    const { resetForm, handleSubmit } = useForm<Step1 | Step2 | Step3>({
      validationSchema: currentSchema,
    });

    const previousStep = () => {
      if (!_stepperObj.value) {
        return;
      }

      currentStepIndex.value--;

      _stepperObj.value.goPrev();
    };

    const handleStep = handleSubmit((values) => {
      for (const item in values) {
        // eslint-disable-next-line no-prototype-builtins
        if (values.hasOwnProperty(item)) {
          if (values[item]) {
            formData.value[item] = values[item];
          }
        }
      }
      if (currentStepIndex.value == 1) {
        const body = {
          phone: formData.value.phone,
          email: formData.value.email,
        };
        ApiService.post("check_mail_or_phone", body as AxiosRequestConfig).then(
          ({ data }) => {
            if (data.email_found) {
              return Swal.fire({
                text: translate("email_exist"),
                icon: "error",
                buttonsStyling: false,
                confirmButtonText: "OK",
                customClass: {
                  confirmButton: "btn fw-bold btn-light-primary",
                },
              });
            } else if (data.phone_found) {
              return Swal.fire({
                text: translate("phone_exist"),
                icon: "error",
                buttonsStyling: false,
                confirmButtonText: "OK",
                customClass: {
                  confirmButton: "btn fw-bold btn-light-primary",
                },
              });
            } else {
              currentStepIndex.value++;

              if (!_stepperObj.value) {
                return;
              }
              _stepperObj.value.goNext();
            }
          }
        );
      } else if (currentStepIndex.value == 2) {
        const body = {
          market_phone: formData.value.market_phone,
          market_email: formData.value.market_email,
        };
        ApiService.post("check_mail_or_phone", body as AxiosRequestConfig).then(
          ({ data }) => {
            if (data.market_email_found) {
              return Swal.fire({
                text: translate("email_exist"),
                icon: "error",
                buttonsStyling: false,
                confirmButtonText: "OK",
                customClass: {
                  confirmButton: "btn fw-bold btn-light-primary",
                },
              });
            } else if (data.market_phone_found) {
              return Swal.fire({
                text: translate("phone_exist"),
                icon: "error",
                buttonsStyling: false,
                confirmButtonText: "OK",
                customClass: {
                  confirmButton: "btn fw-bold btn-light-primary",
                },
              });
            } else {
              currentStepIndex.value++;

              if (!_stepperObj.value) {
                return;
              }
              _stepperObj.value.goNext();
            }
          }
        );
      } else {
        currentStepIndex.value++;

        if (!_stepperObj.value) {
          return;
        }

        _stepperObj.value.goNext();
      }
    });

    //Form submit function
    const formSubmit = () => {
      const body = formData.value;
      // upload image to server
      const form = new FormData();
      // loop to append all form values
      for (const key in body) {
        // eslint-disable-next-line no-prototype-builtins
        if (body.hasOwnProperty(key)) {
          form.append(key, body[key]);
        }
      }
      form.append("market_image", market_image.value as File);
      // Clear existing errors
      store.dispatch(Actions.LOGOUT);

      store
        .dispatch(Actions.REGISTER, form as AxiosRequestConfig)
        .then(() => {
          Swal.fire({
            text: "You have successfully logged in!",
            icon: "success",
            buttonsStyling: false,
            confirmButtonText: "Ok, got it!",
            customClass: {
              confirmButton: "btn fw-bold btn-light-primary",
            },
          }).then(function () {
            hideModal(createAccountModalRef.value);
            resetForm();
            if (!_stepperObj.value) {
              return;
            }
            currentStepIndex.value = 0;
            imgPreview.value = "";
            _stepperObj.value.goFirst();

            router.push({
              name: "home",
              params: { lang: currentLanguage.value },
            });
          });
        })
        .catch(() => {
          Swal.fire({
            text: store.getters.getErrors,
            icon: "error",
            buttonsStyling: false,
            confirmButtonText: "Try again!",
            customClass: {
              confirmButton: "btn fw-bold btn-light-danger",
            },
          });
        });
    };


    const onFileChange = (e) => {
      const file = e.target.files[0];
      if (file) {
        imgPreview.value = URL.createObjectURL(file);
        market_image.value = file;
      } else {
        imgPreview.value = "";
      }
    };

    resetForm({
      values: {
        ...formData.value,
      },
    });

    return {
      createAccountRef,
      totalSteps,
      previousStep,
      handleStep,
      formSubmit,
      onFileChange,
      imgPreview,
      currentLanguage,
      currentLangugeLocale,
      currentStepIndex,
      formData,
      createAccountModalRef,
    };
  },
});
